.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 3rem;
		width: 320px;
		transform: translateY(-90%);
		@include max-lg {
			width: 280px;
			transform: translate(-40%, -90%);
		}
		@include max-md {
			transform: translate(-50%, -90%);
			width: 140px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 320px;
		transform: translateY(-90%);
		@include max-lg {
			width: 280px;
		}
		@include max-md {
			width: 140px;
		}
	}
	.orn-3 {
		position: absolute;
		left: 50%;
		top: 0;
		transform: translate(-50%, -100%);
		width: 380px;
		@include max-md {
			width: 230px;
		}
		@include max-xs {
			width: 200px;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		@include max-md {
			.image-element {
				img {
					height: 160px;
					object-fit: cover;
				}
			}
		}
		@include max-xs {
			.image-element {
				img {
					height: 150px;
				}
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		left: 0;
		width: 340px;
		transform: translate(-100%, -10%);
		@include max-lg {
			transform: translate(-130%, -40%);
		}
		@include max-md {
			width: 200px;
		}
	}
	.orn-6 {
		position: absolute;
		top: 0;
		right: 8rem;
		width: 180px;
		transform: translate(100%, -60%) scaleX(-1);
		@include max-md {
			right: 0;
			width: 140px;
		}
		@include max-xs {
			width: 100px;
		}
	}
	.orn-7 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-20%, -30%);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		img {
			transform-origin: 20% 100%;
		}
		.orn-13 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 160px;
			transform: translate(0, 90%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
		}
	}
	.orn-8 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(20%, -30%) rotate(-65deg);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		img {
			transform-origin: 20% 100%;
		}
		.orn-13 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 160px;
			transform: translate(-65%, 25%) rotate(-45deg);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 240px;
		transform: translate(-30%, 10%);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 240px;
		transform: translate(10%, 10%) scaleX(-1);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			transform: translate(35%, 10%) scaleX(-1);
			width: 140px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 0;
		left: 2rem;
		width: 180px;
		transform: translateY(50%);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
		.orn-14 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(60%, 0%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
		}
	}
	.orn-12 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(-60%, 50%) scaleX(-1);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			transform: translate(-40%, 50%) scaleX(-1);
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
		.orn-14 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 180px;
			transform: translate(40%, 20%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 70%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		.thank-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 500px;
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			background: radial-gradient(ellipse, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.5) 40%, transparent 80%);
			border-radius: 500px;
			@include max-md {
				padding: 2rem 1rem 5rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
