.venue-section {
	position: relative;
	padding-top: 5rem;
	@include max-md {
		padding-top: 3rem;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		border-bottom-left-radius: var(--border-radius);
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 60px;
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		background: linear-gradient(180deg, var(--color-primary), transparent);
		border-radius: var(--border-radius);
		box-shadow: 0 10px 1rem rgba($color: #000000, $alpha: 0.05);
		padding: 5rem 1rem;
		@include max-md {
			padding: 3rem 1rem;
		}
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				padding: 1.5rem 0 0;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
			}
			.card {
				border: 3px solid var(--color-secondary);
				background-color: var(--color-main);
				height: 100%;
				border-radius: 500px;
				z-index: 5;
				text-align: center;
				overflow: hidden;
				.card-body {
					padding: 8rem 2rem;
					@include max-lg {
						padding: 5rem 1rem;
					}
					h4 {
						font-weight: 600;
						margin: 1rem 0;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin-top: 3rem;
		position: relative;
		padding: 2rem;
		border-radius: 10px;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		@include max-md {
			gap: 3rem;
		}
	}
	.event-name {
		position: relative;
		width: 100%;
		.date {
			position: relative;
			gap: 1rem;
			width: 100%;
			display: flex;
			justify-content: center;
			&::before {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%) rotate(45deg);
				width: 1px;
				height: 80%;
				background-color: var(--color-dark);
				content: "";
			}
			h5 {
				font-family: $font-secondary;
				font-weight: 700;
				margin-bottom: 0;
				font-size: 38px;
				min-width: 30%;
				text-align: right;
			}
			p {
				margin-top: 2.25rem;
				font-size: 12px;
				font-weight: 400;
				text-align: left;
				min-width: 30%;
			}
			.with-line {
				width: 70%;
				margin: 0 auto;
				padding: 0.5rem 0;
				border-top: 1px solid var(--color-dark);
				border-bottom: 1px solid var(--color-dark);
				@include max-md {
					width: 100%;
				}
			}
		}
		h4 {
			font-family: $font-secondary;
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.event-place {
		position: relative;
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
