.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 1rem;
		left: 0;
		width: 320px;
		transform: translateX(-60%);
		@include max-lg {
			width: 280px;
		}
		@include max-md {
			width: 240px;
		}
		.orn-2 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 160px;
			transform: translate(100%, 50%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
			img {
				transform-origin: 20% 100%;
			}
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 160px;
			transform: translate(0, 70%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 200px;
			transform: translate(-20%, 80%) rotate(15deg);
			@include max-lg {
				width: 180px;
			}
			@include max-md {
				width: 160px;
			}
			img {
				transform-origin: 50% 100%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 5rem;
		right: 0;
		width: 160px;
		transform: translateX(30%) scaleX(-1);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			top: 20%;
			width: 120px;
		}
		img {
			transform-origin: 20% 100%;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 320px;
			transform: translate(-50%, 30%);
			@include max-lg {
				width: 280px;
			}
			@include max-md {
				width: 240px;
			}
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 160px;
			transform: translate(110%, 50%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 200px;
			transform: translate(-20%, 70%) rotate(15deg);
			@include max-lg {
				width: 180px;
			}
			@include max-md {
				width: 160px;
			}
			img {
				transform-origin: 50% 100%;
			}
		}
	}
	.orn-9 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 180px;
		transform: translateX(-30%);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			top: 65%;
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
		.orn-10 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 180px;
			transform: translate(-10%, 70%) rotate(20deg);
			@include max-lg {
				width: 160px;
			}
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 120px;
			}
			img {
				transform-origin: 50% 100%;
			}
			.orn-11 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 320px;
				transform: translate(-30%, 60%) rotate(-15deg);
				@include max-lg {
					width: 280px;
				}
				@include max-md {
					width: 240px;
				}
				@include max-xs {
					transform: translate(-40%, 60%) rotate(-15deg);
				}
			}
		}
	}
	.orn-12 {
		position: absolute;
		top: 60%;
		right: 0;
		width: 200px;
		transform: translateX(50%);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			top: 70%;
			width: 160px;
		}
		.orn-13 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(-20%, 60%) rotate(-25deg);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			img {
				transform-origin: 50% 100%;
			}
		}
	}
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		color: var(--color-dark);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 24px;
			font-family: $font-secondary;
		}
	}
	.couple-body {
		position: relative;
		background: linear-gradient(180deg, var(--color-primary), transparent);
		padding: 3rem 2rem;
		border-radius: var(--border-radius);
		@include max-md {
			padding: 2rem 1rem;
		}
	}
	.couple-content {
		position: relative;
		margin-bottom: 2rem;
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 3/4;
			border-radius: 500px;
			margin: 0 auto;
			border: 3px solid var(--color-secondary);
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
				border-radius: 500px;
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
	.reminder-wrap {
		.orn-1 {
			position: absolute;
			top: 0;
			left: 3rem;
			width: 320px;
			transform: translateY(-90%);
			@include max-lg {
				width: 280px;
				transform: translate(-50%, -90%);
			}
			@include max-md {
				transform: translate(-50%, -90%);
				width: 140px;
			}
		}
		.orn-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 320px;
			transform: translateY(-90%);
			@include max-lg {
				width: 280px;
			}
			@include max-md {
				width: 140px;
			}
		}
		.orn-3 {
			position: absolute;
			left: 50%;
			top: 0;
			transform: translate(-50%, -100%);
			width: 380px;
			@include max-lg {
				width: 280px;
			}
			@include max-md {
				width: 180px;
			}
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			@include max-md {
				.image-element {
					img {
						object-fit: cover;
					}
				}
			}
		}
		.orn-5 {
			position: absolute;
			top: 0;
			left: 0;
			width: 340px;
			transform: translate(-100%, -10%);
			@include max-lg {
				width: 280px;
				transform: translate(-130%, -40%);
			}
			@include max-md {
				width: 160px;
			}
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 8rem;
			width: 180px;
			transform: translate(100%, -60%) scaleX(-1);
			@include max-lg {
				right: 2rem;
			}
			@include max-md {
				right: 0;
				width: 140px;
			}
		}
		.orn-7 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: translate(-20%, -30%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			img {
				transform-origin: 20% 100%;
			}
			.orn-13 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 120px;
				transform: translate(0, 90%);
				@include max-lg {
					width: 100px;
				}
				@include max-md {
					width: 80px;
				}
			}
		}
		.orn-9 {
			position: absolute;
			top: unset;
			bottom: 0;
			left: 0;
			width: 240px;
			transform: translate(-30%, 10%);
			@include max-lg {
				width: 200px;
			}
			@include max-md {
				display: none;
				width: 140px;
			}
			@include max-xs {
				width: 140px;
			}
			img {
				transform-origin: 50% 100%;
			}
		}
		.orn-10 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 240px;
			transform: translate(10%, 10%) scaleX(-1);
			@include max-lg {
				width: 200px;
			}
			@include max-md {
				transform: translate(35%, 10%) scaleX(-1);
				width: 140px;
			}
			@include max-xs {
				width: 140px;
			}
			img {
				transform-origin: 50% 100%;
			}
		}
		.orn-11 {
			position: absolute;
			bottom: 0;
			left: 2rem;
			width: 180px;
			transform: translateY(50%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				left: 0;
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
			.orn-14 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 180px;
				transform: translate(60%, 0%);
				@include max-lg {
					width: 140px;
				}
				@include max-md {
					width: 120px;
				}
				@include max-xs {
					width: 100px;
				}
			}
		}
		.orn-12 {
			position: absolute;
			top: unset;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(-60%, 50%) scaleX(-1);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				transform: translate(-40%, 50%) scaleX(-1);
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
			.orn-14 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 180px;
				transform: translate(40%, 20%);
				@include max-lg {
					width: 140px;
				}
				@include max-md {
					width: 120px;
				}
				@include max-xs {
					width: 100px;
				}
			}
		}
	}
}
