.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: transparent;
	overflow: hidden;
	height: 100vh;
	width: 100%;
	@for $i from 1 through 6 {
		.orn-#{$i} {
			transition: transform 2.5s ease;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 90%;
		transform: translate(-35%, -30%) rotate(70deg);
		@include max-lg {
			top: 20%;
			width: 120%;
			transform: translate(-35%, 0%) rotate(40deg);
		}
		@include max-md {
			top: 25%;
			width: 150%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		left: 50%;
		width: 90%;
		transform: translate(-50%, -30%) rotate(90deg);
		@include max-lg {
			left: 0;
			width: 120%;
			transform: translate(-30%, -30%) rotate(60deg);
		}
		@include max-md {
			width: 150%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 90%;
		transform: translate(40%, -30%);
		@include max-lg {
			width: 120%;
			transform: translate(20%, -20%);
		}
		@include max-md {
			width: 150%;
			transform: translate(20%, 0%);
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 90%;
		transform: translate(-20%, 35%);
		@include max-lg {
			width: 120%;
			transform: translate(-20%, 25%);
		}
		@include max-md {
			width: 150%;
			transform: translate(-20%, 10%);
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 50%;
		width: 90%;
		transform: translate(50%, 30%) scaleY(-1);
		@include max-lg {
			right: 0;
			width: 120%;
			transform: translate(20%, 20%) scaleY(-1) rotate(-20deg);
		}
		@include max-md {
			width: 150%;
			transform: translate(20%, 0%) scaleY(-1) rotate(-20deg);
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 90%;
		transform: translate(30%, 20%) scaleY(-1) rotate(15deg);
		@include max-lg {
			bottom: 30%;
			width: 120%;
			transform: translate(30%, 0%) scaleY(-1) rotate(15deg);
		}
		@include max-md {
			width: 150%;
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		left: 20%;
		width: 100%;
		transform: translate(0, 40%) scaleY(-1) rotate(90deg);
		@include max-lg {
			width: 120%;
		}
		@include max-md {
			left: 0;
			width: 150%;
			transform: translate(-30%, 40%) scaleY(-1) rotate(90deg);
		}
	}
	.orn-8 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 160px;
		transform: translate(-20%, -50%);
		@include max-lg {
			width: 180px;
			transform: translate(30%, -50%);
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 130px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	&.cover-opening {
		.orn-1 {
			transform: translate(-100%, -100%) rotate(70deg);
		}
		.orn-2 {
			transform: translate(-50%, -100%) rotate(90deg);
		}
		.orn-3 {
			transform: translate(100%, -90%);
		}
		.orn-4 {
			transform: translate(-100%, 95%);
		}
		.orn-5 {
			transform: translate(60%, 100%) scaleY(-1);
		}
		.orn-6 {
			transform: translate(100%, 80%) scaleY(-1) rotate(15deg);
		}
		.orn-7 {
			transform: translate(-10%, 100%) scaleY(-1) rotate(90deg);
		}
		.cover-wrapper {
			opacity: 0;
		}
	}
	&.cover-opened {
		display: none;
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 1rem 0;
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: rgba($color: #fff, $alpha: 0.8);
					content: "";
					border-radius: var(--border-radius);
				}
				@include max-lg {
					flex-direction: column;
					margin-top: 10rem;
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.image-wrapper {
					position: relative;
					width: 240px;
					height: auto;
					aspect-ratio: 3/4;
					border-radius: 500px;
					@include max-lg {
						width: 50%;
						margin: 0 auto;
						margin-top: -10rem;
					}
					@include max-md {
						width: 70%;
						height: 100%;
					}
					@include max-xs {
						width: 60%;
					}
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 500px;
						border: 2px solid var(--color-secondary);
						position: relative;
						@include min-lg {
							transform: translateX(-30%);
						}
					}
				}
				.cover-description {
					padding: 0 2rem;
					text-align: center;
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					* {
						position: relative;
					}
					.title {
						color: var(--color-dark);
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
