.wishes-section {
	position: relative;
	padding: 5rem 0;
	background: linear-gradient(180deg, var(--color-primary), transparent);
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		width: 300px;
		top: 0;
		left: 0;
		transform: translate(-80%, -10%);
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 240px;
		}
		.orn-2 {
			width: 140px;
			transform: translate(120%, -30%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				transform: translate(140%, -30%);
				width: 100px;
			}
			img {
				transform-origin: 50% 100%;
			}
			.orn-3 {
				width: 140px;
				transform: translate(20%, -30%);
				@include max-lg {
					width: 120px;
				}
				@include max-md {
					width: 100px;
				}
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 5rem;
		right: 0;
		width: 160px;
		transform: translateX(20%) scaleX(-1);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
		.orn-5 {
			width: 180px;
			transform: translate(-20%, 5rem);
			@include max-lg {
				width: 160px;
			}
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 120px;
			}
			img {
				transform-origin: 20% 100%;
			}
		}
	}
	.elements-absolute {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 70%;
		.background-element {
			display: block;
			height: 100%;
			.background-wrapper {
				width: 100%;
				height: 100%;
				display: inline-block;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}
	.card-form {
		margin-bottom: 0;
		background-color: transparent;
		position: relative;
		z-index: 3;
		&.card {
			border: none;
		}
		.card-body {
			padding: 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}

	.wishes-preview {
		position: relative;
		@include max-lg {
			margin-top: 2rem;
		}
		.card {
			border: none;
			position: relative;
			background-color: transparent;
			border-radius: var(--border-radius);
			.card-body {
				overflow: hidden;
				padding: 2rem 0;
				.wishes {
					text-align: left;
					height: 28rem;
					overflow: auto;
					padding-right: 1rem;
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}
					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						display: grid;
						grid-template-columns: auto 1fr;
						gap: 1rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						.wish-badge {
							background-color: var(--color-light);
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50px;
						}
						.wish-description {
							padding: 1rem;
							background-color: var(--color-light);
							border-radius: 5px;
							position: relative;
							&::before {
								position: absolute;
								top: 15px;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: var(--color-light);
							}
						}
					}
				}
			}
		}
	}
}
