.rsvp-section {
	position: relative;
	padding: 3rem 0 5rem;
	@include max-md {
		padding: 1rem 0 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-35%, -30%);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		.orn-2 {
			width: 280px;
			transform: translate(-55%, -30%);
			@include max-md {
				width: 240px;
			}
			.orn-3 {
				width: 180px;
				transform: translate(60%, -30%) rotate(15deg);
				@include max-lg {
					width: 160px;
				}
				@include max-md {
					width: 140px;
				}
				img {
					transform-origin: 50% 100%;
				}
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 5rem;
		right: 0;
		width: 180px;
		transform: translateX(20%) scaleX(-1);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
		}
		img {
			transform-origin: 20% 100%;
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 3rem;
		border-radius: var(--border-radius);
		border: 3px solid var(--color-secondary);
		@include max-md {
			padding: 2rem;
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
		}
	}
}
