.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.elements-absolute {
		display: flex;
		align-items: flex-end;
	}
	.orn-1 {
		position: absolute;
		top: 5rem;
		left: 0;
		width: 280px;
		transform: translateX(-60%) rotate(5deg);
		@include max-lg {
			width: 240px;
		}
		@include max-md {
			transform: translateX(-70%) rotate(5deg);
			width: 200px;
		}
		.orn-2 {
			width: 180px;
			transform: translate(50%, -25%) rotate(5deg);
			@include max-lg {
				width: 160px;
			}
			@include max-md {
				width: 140px;
			}
			img {
				transform-origin: 50% 100%;
			}
		}
		.orn-3 {
			width: 140px;
			transform: translate(40%, -10%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 5rem;
		right: 0;
		width: 240px;
		transform: translateX(40%);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			transform: translateX(50%);
			width: 200px;
		}
		.orn-5 {
			width: 180px;
			transform: translate(30%, -25%) scaleX(-1) rotate(15deg);
			@include max-lg {
				width: 160px;
			}
			@include max-md {
				width: 140px;
			}
			img {
				transform-origin: 50% 100%;
			}
			.orn-6 {
				width: 140px;
				transform: translate(40%, -10%);
				@include max-lg {
					width: 120px;
				}
				@include max-md {
					width: 100px;
				}
			}
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 3rem 0 0;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		.story-background {
			position: relative;
			object-fit: cover;
			width: 100%;
			height: 100%;
			border-radius: 500px;
		}
	}
}
